/* Mobile Profile Page -   */
.profilePage {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1500;
  background: var(--theme-background);
  display: flex;
  flex-direction: column;
  animation: slideInFromBottom 0.35s cubic-bezier(0.4, 0, 0.2, 1);
  will-change: transform;
  transform: translateZ(0);
  overflow-y: auto;
}

.pageHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 20px;
  border-bottom: 1px solid var(--theme-border-color, rgba(255, 255, 255, 0.1));
  background: var(--theme-background);
  backdrop-filter: var(--theme-backdrop-filter, none);
  -webkit-backdrop-filter: var(--theme-backdrop-filter, none);
  position: sticky;
  top: 0;
  z-index: 10;
}

.backButton {
  width: 44px;
  height: 44px;
  border: none;
  background: var(--theme-card-background, rgba(255, 255, 255, 0.05));
  border-radius: 12px;
  color: var(--theme-text-primary);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 
    2px 2px 4px rgba(0, 0, 0, 0.1),
    -2px -2px 4px rgba(255, 255, 255, 0.05);
}

.backButton:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-2px);
  box-shadow: 
    4px 4px 8px rgba(0, 0, 0, 0.15),
    -4px -4px 8px rgba(255, 255, 255, 0.1);
}

.pageTitle {
  font-size: 20px;
  font-weight: 700;
  color: var(--theme-text-primary);
  margin: 0;
  background: linear-gradient(135deg, var(--theme-main-color, #d0bcff), #b69df8);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.saveButton {
  width: 44px;
  height: 44px;
  border: none;
  background: var(--theme-main-color, #d0bcff);
  border-radius: 12px;
  color: white;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 
    2px 2px 4px rgba(0, 0, 0, 0.1),
    -2px -2px 4px rgba(255, 255, 255, 0.05);
}

.saveButton:hover {
  background: #b69df8;
  transform: translateY(-2px);
  box-shadow: 
    4px 4px 8px rgba(0, 0, 0, 0.15),
    -4px -4px 8px rgba(255, 255, 255, 0.1);
}

/*    */
.userInfoSection {
  padding: 24px 20px;
  text-align: center;
  border-bottom: 1px solid var(--theme-border-color, rgba(255, 255, 255, 0.1));
}

.userAvatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: var(--theme-main-color, #d0bcff);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 16px;
  overflow: hidden;
  border: 3px solid rgba(255, 255, 255, 0.2);
  box-shadow: 
    4px 4px 8px rgba(0, 0, 0, 0.1),
    -4px -4px 8px rgba(255, 255, 255, 0.05);
}

.avatarImage {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.avatarIcon {
  color: white;
}

.userDetails {
  text-align: center;
}

.userName {
  font-size: 24px;
  font-weight: 700;
  color: var(--theme-text-primary);
  margin: 0 0 8px 0;
}

.userEmail {
  font-size: 16px;
  color: var(--theme-text-secondary);
  margin: 0;
}

/*   */
.sectionsContainer {
  flex: 1;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.section {
  background: var(--theme-card-background, rgba(255, 255, 255, 0.05));
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 
    4px 4px 8px rgba(0, 0, 0, 0.1),
    -4px -4px 8px rgba(255, 255, 255, 0.05);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.sectionButton {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 20px;
  background: transparent;
  border: none;
  color: var(--theme-text-primary);
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  text-align: left;
}

.sectionButton:hover {
  background: rgba(255, 255, 255, 0.05);
}

.activeSection {
  background: rgba(208, 188, 255, 0.1);
  color: var(--theme-main-color, #d0bcff);
}

.activeSection svg {
  color: var(--theme-main-color, #d0bcff);
}

.editIcon {
  margin-left: auto;
  color: var(--theme-text-secondary);
  transition: color 0.2s ease;
}

.activeSection .editIcon {
  color: var(--theme-main-color, #d0bcff);
}

.sectionContent {
  animation: slideDown 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.sectionPanel {
  padding: 0 20px 20px 20px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.settingCard {
  background: rgba(255, 255, 255, 0.03);
  border-radius: 12px;
  padding: 16px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.settingInfo {
  margin-bottom: 12px;
}

.settingLabel {
  font-size: 16px;
  font-weight: 600;
  color: var(--theme-text-primary);
  margin: 0 0 4px 0;
}

.settingDescription {
  font-size: 14px;
  color: var(--theme-text-secondary);
  margin: 0;
  line-height: 1.4;
}

.settingControl {
  width: 100%;
}

/*   */
.textInput {
  width: 100%;
  padding: 12px 16px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  color: var(--theme-text-primary);
  font-size: 16px;
  transition: all 0.2s ease;
}

.textInput:focus {
  outline: none;
  border-color: var(--theme-main-color, #d0bcff);
  background: rgba(255, 255, 255, 0.1);
}

.select {
  width: 100%;
  padding: 12px 16px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  color: var(--theme-text-primary);
  font-size: 16px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.select:focus {
  outline: none;
  border-color: var(--theme-main-color, #d0bcff);
}

.themeButtons {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.themeButton {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 16px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  color: var(--theme-text-primary);
  font-size: 16px;
  cursor: pointer;
  transition: all 0.2s ease;
  text-align: left;
}

.themeButton:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: var(--theme-main-color, #d0bcff);
}

.themeButton.active {
  background: var(--theme-main-color, #d0bcff);
  border-color: var(--theme-main-color, #d0bcff);
  color: white;
}

.animationButtons {
  display: flex;
  gap: 12px;
  justify-content: center;
}

.animationButton {
  width: 48px;
  height: 48px;
  border: 2px solid rgba(128, 128, 128, 0.4);
  border-radius: 12px;
  background: transparent;
  color: var(--theme-text-primary);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 
    2px 2px 4px rgba(0, 0, 0, 0.1),
    -2px -2px 4px rgba(255, 255, 255, 0.05);
}

.animationButton:hover {
  background: rgba(128, 128, 128, 0.1);
  border-color: var(--theme-main-color, #d0bcff);
  color: var(--theme-main-color, #d0bcff);
  transform: translateY(-2px);
}

.animationButton.active {
  background: var(--theme-main-color, #d0bcff);
  border-color: var(--theme-main-color, #d0bcff);
  color: white;
  transform: scale(1.05);
  box-shadow: 
    4px 4px 12px rgba(208, 188, 255, 0.4),
    -4px -4px 12px rgba(255, 255, 255, 0.1),
    0 0 0 1px rgba(208, 188, 255, 0.3);
}

.toggleSwitch {
  width: 56px;
  height: 32px;
  border: 2px solid rgba(128, 128, 128, 0.4);
  border-radius: 16px;
  background: transparent;
  cursor: pointer;
  position: relative;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.toggleSwitch.active {
  background: var(--theme-main-color, #d0bcff);
  border-color: var(--theme-main-color, #d0bcff);
}

.toggleSlider {
  width: 24px;
  height: 24px;
  background: rgba(128, 128, 128, 0.6);
  border-radius: 50%;
  position: absolute;
  top: 2px;
  left: 2px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.toggleSwitch.active .toggleSlider {
  background: white;
  transform: translateX(24px);
}

.avatarUpload {
  display: flex;
  align-items: center;
  gap: 16px;
}

.avatarPreview {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: var(--theme-main-color, #d0bcff);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  overflow: hidden;
}

.avatarPreview img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.uploadButton {
  padding: 12px 20px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  color: var(--theme-text-primary);
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.uploadButton:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: var(--theme-main-color, #d0bcff);
}

.actionButton {
  padding: 12px 20px;
  background: var(--theme-main-color, #d0bcff);
  border: none;
  border-radius: 12px;
  color: white;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.actionButton:hover {
  background: #b69df8;
  transform: translateY(-1px);
}

/*   */
.logoutSection {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid var(--theme-border-color, rgba(255, 255, 255, 0.1));
}

.logoutButton {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 20px;
  background: rgba(244, 67, 54, 0.1);
  border: 1px solid rgba(244, 67, 54, 0.2);
  border-radius: 16px;
  color: #f44336;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.logoutButton:hover {
  background: rgba(244, 67, 54, 0.2);
  border-color: rgba(244, 67, 54, 0.3);
  transform: translateY(-2px);
}

/*  */
@keyframes slideInFromBottom {
  from {
    opacity: 0;
    transform: translateY(100%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideDown {
  from {
    opacity: 0;
    max-height: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    max-height: 1000px;
    transform: translateY(0);
  }
}

/*   */
.infoCard {
  margin-top: 20px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
}

.appInfo {
  text-align: center;
}

.appName {
  font-size: 18px;
  font-weight: 600;
  color: var(--theme-text-primary);
  margin-bottom: 8px;
}

.appVersion {
  font-size: 14px;
  color: var(--theme-text-secondary);
  margin-bottom: 12px;
}

.appDescription {
  font-size: 14px;
  color: var(--theme-text-secondary);
  line-height: 1.5;
}

/*   */
.animationButtons {
  display: flex;
  gap: 8px;
}

.animationButton {
  width: 40px;
  height: 40px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.05);
  color: var(--theme-text-secondary);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  transition: all 0.2s ease;
}

.animationButton:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.3);
}

.animationButton.active {
  background: var(--theme-main-color, #d0bcff);
  border-color: var(--theme-main-color, #d0bcff);
  color: white;
}

/*   */
[data-theme="light"] .profilePage {
  background: var(--theme-background);
}

[data-theme="light"] .pageHeader {
  border-bottom-color: rgba(0, 0, 0, 0.1);
}

[data-theme="light"] .backButton {
  background: rgba(0, 0, 0, 0.03);
  box-shadow: 
    2px 2px 4px rgba(0, 0, 0, 0.1),
    -2px -2px 4px rgba(255, 255, 255, 0.8);
}

[data-theme="light"] .section {
  background: rgba(0, 0, 0, 0.03);
  box-shadow: 
    4px 4px 8px rgba(0, 0, 0, 0.1),
    -4px -4px 8px rgba(255, 255, 255, 0.8);
}

[data-theme="light"] .settingCard {
  background: rgba(0, 0, 0, 0.03);
  border-color: rgba(0, 0, 0, 0.1);
}

[data-theme="light"] .textInput,
[data-theme="light"] .select {
  background: rgba(0, 0, 0, 0.03);
  border-color: rgba(0, 0, 0, 0.1);
}

[data-theme="light"] .themeButton {
  background: rgba(0, 0, 0, 0.03);
  border-color: rgba(0, 0, 0, 0.1);
}

[data-theme="light"] .logoutSection {
  border-top-color: rgba(0, 0, 0, 0.1);
}

/*  */
@media (max-width: 480px) {
  .pageHeader {
    padding: 12px 16px;
  }
  
  .pageTitle {
    font-size: 18px;
  }
  
  .userInfoSection {
    padding: 20px 16px;
  }
  
  .userAvatar {
    width: 70px;
    height: 70px;
  }
  
  .userName {
    font-size: 20px;
  }
  
  .sectionsContainer {
    padding: 16px;
  }
  
  .sectionButton {
    padding: 16px;
  }
  
  .sectionPanel {
    padding: 0 16px 16px 16px;
  }
  
  .settingCard {
    padding: 12px;
  }
}
